import { Component, Renderer2, OnInit} from '@angular/core';
import { BooksService } from './books.service';
import {
  Directive, ElementRef, OnChanges, ViewContainerRef } from '@angular/core';
@Component({
  selector: 'app-books',
  templateUrl: './books.component.html',
  styleUrls: ['./books.component.css'],
  providers: [BooksService]
})

export class BooksComponent implements OnInit {
  componentData = null;
  books = [];
  constructor(private booksService: BooksService, private viewContainerRef: ViewContainerRef) {}
  ngOnInit() {
    console.log('this.books');
  }
  updateBookList(query: string) {
    this.showBooks(query);
  }

  showBooks(query) {
    this.books = [];
    this.booksService.getBooks(query)
      .subscribe((response) => {
        const items = response['items'];
        for (const item of items) {
          const book = {
            title: '',
            imageLinks: '',
            description: ''
          };
          book.title = item.volumeInfo.title;
          if (item.volumeInfo.imageLinks) {
            book.imageLinks = item.volumeInfo.imageLinks.smallThumbnail;
          }
          if (item.volumeInfo.description) {
            book.description = item.volumeInfo.description;
          }
          if (book !== undefined) {
            this.books.push(book);
          }
        }
        return this.books;
      });
  }
}
